li:after {
  content: " ";
  position: absolute;
  left: 45%;
  opacity: 0;
  margin: 0 auto;
  bottom: 0px;
  border: 5px solid transparent;
  border-bottom-color: #ddd;
  transition: 0.1s ease-in-out;
}
li.active:after {
  content: " ";
  position: absolute;
  left: 45%;
  opacity: 1;
  margin: 0 auto;
  bottom: 0px;
  border: 10px solid transparent;
  border-bottom-color: #ddd;
}
.nav-tabs > li a {
  width: 70px;
  height: 70px;
  margin: 20px auto;
  border-radius: 100%;
  padding: 0;
}
.nav-tabs > li a:hover {
  background: transparent;
}
.tab-content {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}
.tab-pane {
  position: relative;
  padding-top: 20px;
}
.tab-content .head {
  font-size: 25px;
  text-transform: uppercase;
  padding-bottom: 10px;
}
.btn-outline-rounded {
  padding: 10px 40px;
  margin: 20px 0;
  border: 2px solid transparent;
  border-radius: 25px;
}
.btn.green {
  background-color: #5cb85c;
  /*border: 2px solid #5cb85c;*/
  color: #ffffff;
}
@media (max-width: 585px) {
  .board {
    width: 90%;
    height: auto !important;
  }
  span.round-tabs {
    font-size: 16px;
    width: 50px;
    height: 50px;
    line-height: 50px;
  }
  .tab-content .head {
    font-size: 20px;
  }
  .nav-tabs > li a {
    width: 50px;
    height: 50px;
    line-height: 50px;
  }
  li.active:after {
    content: " ";
    position: absolute;
    left: 35%;
  }
  .btn-outline-rounded {
    padding: 12px 20px;
  }
}
.clickable {
  cursor: pointer;
}
.panel-heading span {
  margin-top: -20px;
  font-size: 15px;
}
/*---------- Correcciones para Chat y timeline dentro de un panel--------*/
.tab-pane .timeline {
  background: #ECF0F5;
  padding-bottom: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
.timeline:before {
  top: 10px;
  left: 41px;
}
.box {
  border: 1px solid #cccccc;
}
.btn-box-tool {
  color: #ffffff;
}
.box.box-primary {
  border-top-color: #cccccc;
}
.direct-chat-primary .right > .direct-chat-text {
  background: #d9edf7;
  border-color: #d9edf7;
  color: #444;
}
.direct-chat-primary .right > .direct-chat-text:after,
.direct-chat-primary .right > .direct-chat-text:before {
  border-left-color: #d9edf7;
}
/*------------------------------------------------------------------------*/
.fix-well-button {
  padding-top: 7px;
  padding-right: 10px;
  padding-bottom: 40px;
}
